<template>
  <div class="auto_ten">
    <el-table
      :data="identityList"
      stripe
      style="width: 100%">
      <el-table-column
        prop="shopName"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="location"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import * as merchantApi from '../../../api/Merchant';
import * as shopApi from '../../../api/Shop';

export default {
  data() {
    return {
      merchantBasicList: [],
      merchantIdList: [],
      merchantShopIdList: [],
      identityList: []
    }
  },
  beforeMount() {
    let _this = this
    merchantApi.getMerchantBasicListNotChecked(1).then(res => {
      if(res.status != 200) this.$message.error("");

      else {
        this.merchantBasicList = res.body;
      } 
      return
    }).then(() => {
      for(let i of this.merchantBasicList) {
        this.merchantIdList.push({
          "Id": i.authPkId
        });
        this.merchantShopIdList.push({
          "ShopId": i.shopId
        });
      }
      merchantApi.getMerchantsName(this.merchantIdList).then(res => {
        if(res.status != 200) this.$message.error("");

        else {
          for(let i of res.body) {
            this.identityList.push({
              "name": i,
              "shopName": "",
              "location": ""
            })
          }
          shopApi.GetShopListByShopIds(this.merchantShopIdList).then(res => {
            if(res.status != 200) this.$message.error("");
          
            else {
              for(let i = 0; i < res.body.length; i++) {
                _this.identityList[i].shopName = res.body[i].shopName;
                _this.identityList[i].location = res.body[i].location;
              }
            }
          })
        }
      })
    })
  },
  methods: {
    handleClick(row) {
      let _this = this
      for(let i = 0; i <  this.identityList.length; i++) {
        if(_this.identityList[i].name == row.name) {
          this.$store.dispatch('commitIdentityMerchantId', this.merchantIdList[i].Id)
        }
      }
      
      this.$router.push('/Manager/IdentityDetail');
    }
  }
}
</script>
<style lang="less" scoped>
  @import "../../../less/container.less";
</style>
