<template>
  <div>
    <div>
      <span>: </span>
      <el-select v-model="selectedRole" placeholder="" @change="changeRole">
        <el-option
          key=""
          label=""
          value="customer">
        </el-option>
        <el-option
          key=""
          label=""
          value="merchant">
        </el-option>
      </el-select>
    </div>
    <el-table
      :data="okList"
      stripe
      style="width: 100%">
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleAdd(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="10"
      current-change="changeList">
    </el-pagination>
    <el-dialog title="" :visible.sync="showAddToBlackDialog" style="text-align:left;">
      <div>{{selectedItemName}}?</div>
      <div style="margin-top: 2rem;">: <el-input v-model="vioMemberData.Detail" placeholder=""></el-input></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddToBlackDialog = false"> </el-button>
        <el-button type="primary" @click="addToBlack"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import * as customerApi from '../../../api/User';
import * as merchantApi from '../../../api/Merchant';
import * as managerApi from '../../../api/Manager';

export default{
  data() {
    return {
      selectedRole: "customer",
      okList: [],
      customerList: [{
        "pkId": "",
        "lookingImg": "",
        "address": null,
        "email": "",
        "userName": null
      }],
      merchantList: [{
        "authPkId": "",
        "shopId": "",
        "isChecked": false
      }],
      merchantNameList: [],
      merchantIdList: [],
      showAddToBlackDialog: false,
      selectedItemIndex: 0,
      selectedItemName: "",
      vioMemberData: {
        "UserName": "",
        "Date": "",
        "Detail": "",
        "Role": 2,
        "ManagerId": ""
      }
    }
  },
  beforeCreate() {
    customerApi.getCustomers(1).then(res => {
      if(res.status != 200) this.$message.error();

      else {
        this.customerList = res.body;

        for(let i of this.customerList)
        {
          this.okList.push({
            'name': i.email
          })
        }
      }
    })
  },
  methods: {
    handleAdd(index, role) {
      this.showAddToBlackDialog = true
      this.selectedItemIndex = index
      this.selectedItemName = role.name
    },
    addToBlack() {
      this.showAddToBlackDialog = false;
      var now = new Date();

      this.vioMemberData.UserName = this.selectedItemName
      this.vioMemberData.Date = now.getFullYear() + "/" + now.getMonth() + "/" + now.getDate() + " " 
                              + now.getHours() + ":" + now.getMinutes() + ":" + now.getSeconds();
      if(this.selectedRole == "customer")   this.vioMemberData.Role = 1;
      if(this.selectedRole == "merchant")this.vioMemberData.Role = 2;
      this.vioMemberData.ManagerId = this.$store.getters.getManagerId;

      managerApi.setViolateUser(this.vioMemberData).then(res => {
        if(res.status != 201) this.$message.error("");

        else {
          if(this.selectedRole == "customer") {
            customerApi.getCustomers(1).then(res => {
              if(res.status != 200) this.$message.error();

              else {
                this.customerList = res.body;

                for(let i of this.customerList)
                {
                  this.okList.push({
                    'name': i.email
                  })
                }
              }
            })
          } else if(this.selectedRole == "merchant")
          {
            merchantApi.getMerchantBasics(1).then(res => {
              if(res.status != 200) this.$message.error();

              else this.merchantList = res.body;

              return
            }).then(() => {
              for(let i of this.merchantList) {
                this.merchantIdList.push({
                  "Id": i.authPkId
                });
              }
              merchantApi.getMerchantsName(this.merchantIdList).then(res => {
                if(res.status != 200) this.$message.error("");

                else {
                  this.merchantNameList = res.body
                  this.okList = [];
                  for(let i of this.merchantNameList)
                  {
                    this.okList.push({
                      'name': i
                    })
                  }
                }
              })
            })
          }
        }
      })
    },
    changeRole(role) {
      if(role == 'customer') {
        this.okList = []
        customerApi.getCustomers(1).then(res => {
          if(res.status != 200) this.$message.error();

          else {
            this.customerList = res.body;

            for(let i of this.customerList)
            {
              this.okList.push({
                'name': i.email
              })
            }
          }
        })
      } else if (role == 'merchant') {
        this.okList = []
        merchantApi.getMerchantBasics(1).then(res => {
          if(res.status != 200) this.$message.error();

          else this.merchantList = res.body;

          return
        }).then(() => {
          for(let i of this.merchantList) {
            this.merchantIdList.push({
              "Id": i.authPkId
            });
          }
          merchantApi.getMerchantsName(this.merchantIdList).then(res => {
            if(res.status != 200) this.$message.error("");

            else {
              this.merchantNameList = res.body
              this.okList = [];
              for(let i of this.merchantNameList)
              {
                this.okList.push({
                  'name': i
                })
              }
            }
          })
        })
      }
    },
    changeList(curPage) {
      if(this.selectedRole == "customer") {
        this.okList = []
        customerApi.getCustomers(curPage).then(res => {
          if(res.status != 200) this.$message.error();

          else {
            this.customerList = res.body;

            for(let i of this.customerList)
            {
              this.okList.push({
                'name': i.email
              })
            }
          }
        })
      } else if(this.selectedRole == "merchant") {
        this.okList = []
        merchantApi.getMerchantBasics(curPage).then(res => {
          if(res.status != 200) this.$message.error();

          else this.merchantList = res.body;

          return
        }).then(() => {
          for(let i of this.merchantList) {
            this.merchantIdList.push({
              "Id": i.authPkId
            });
          }
          merchantApi.getMerchantsName(this.merchantIdList).then(res => {
            if(res.status != 200) this.$message.error("");

            else {
              this.merchantNameList = res.body
              this.okList = [];
              for(let i of this.merchantNameList)
              {
                this.okList.push({
                  'name': i
                })
              }
            }
          })
        })
      }
    }
  }
}
</script>
<style lang="less" scoped>

</style>