<template>
  <div class="auto_eight">
    <div  style="margin-top: 3rem; background: lightblue; border: 1px solid blue;">
      <div class="left-five itemShow">: {{identity.identityName}}</div>
      <div class="left-five itemShow">: {{identity.identityNum}}</div>
      <div class="left-five itemShow">: {{identity.licenseCode}}</div>
      <div class="left-five itemShow">: {{identity.licenseName}}</div>
      <div class="left-five itemShow">: {{identity.licenseOwner}}</div>
      <div class="left-five itemShow">: {{identity.availableSatartTime}}</div>
      <div class="left-five itemShow">: {{identity.availableTime}}</div>
      <div class="left-five itemShow">: {{identity.tel}}</div>
      <div>
        <div class="imgShow"><p>: </p><img :src="identity.identityImgF" alt="" style="width: 10rem; height: 10rem;"/></div>
        <div class="imgShow"><p>: </p><img :src="identity.identityImgB" alt="" style="width: 10rem; height: 10rem;"/></div>
        <div class="imgShow"><p>: </p><img :src="identity.licenseImg" alt="" style="width: 10rem; height: 10rem;"/></div>
      </div>
      <div style="clear:both;"></div>
    </div>
    <div>
      <el-button type="success" @click="passCheck" style="margin: 2rem 2rem 0 2rem;"></el-button>
      <el-button type="danger" @click="failCheck" style="margin: 2rem 2rem 0 2rem;"></el-button>
    </div>
  </div>
</template>
<script>
  import * as merchantApi from '../../../api/Merchant';

  export default {
    data() {
      return {
        identity: {

        }
      }
    },
    beforeMount() {
      merchantApi.getMerchantIdentityByMerchantId(this.$store.getters.getIdentityMerchantId).then(res => {
        if(res.status != 200) this.$message.error('get identity error');

        else this.identity = res.body;
      })
    },
    methods: {
      passCheck() {
        merchantApi.checkIdentity({
          "MerchantId": this.$store.getters.getIdentityMerchantId,
          "CheckResult": true
        }).then(res => {
          if(res.status != 200) this.$message.error();

          else {
            this.$message({
              type: 'success',
              message: ''
            })
            this.$router.push('/Manager/IdentityList');
          }
        })
      },
      failCheck() {
        this.$message({
          type: 'warning',
          message: ''
        })
        this.$router.push('/Manager/IdentityList');
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../less/container.less";

  @left_five_height : auto;
  .imgShow {
    float: left;
    margin-left: 8rem;
  }
  .itemShow {
    height: 3rem;
    margin-top: 0.3rem;
  }
</style>