<template>
  <div class="auto_ten">
    <el-table
      :data="violateList"
      stripe
      style="width: 100%">
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="role"
        label=""
        width="180">
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="10"
      current-change="changeList">
    </el-pagination>
  </div>
</template>
<script>
import * as managerApi from '../../../api/Manager';

export default {
  data() {
    return {
      violateList: [],
      roles: {
        1: "",
        2: ""
      }
    }
  },
  beforeMount() {
    managerApi.getViolateUsers(1).then(res => {
      if(res.status != 200) this.$message.error();

      else {
        this.violateList = res.body

        for(let i = 0; i < this.violateList.length; i++) {
          this.violateList[i].role = this.violateList[i].role == 1 ? "" : "";
        }
      }
    })
  },
  methods: {
    changeList(curPage) {
      managerApi.getViolateUsers(curPage).then(res => {
        if(res.status != 200) this.$message.error();

        else {
          this.violateList = res.body

          for(let i = 0; i < this.violateList.length; i++) {
            this.violateList[i].role = this.violateList[i].role == 1 ? "" : "";
          }
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
  @import '../../../less/container';
</style>
