<template>
  <div class="auto_ten" style="background: #e4eee1;">
    <div class="auto_six" style="background: #f1fcee;">
      <div style="height: 10rem; border-bottom: 1px solid gray; 
                  font-size: 3rem; font-weight: bold; text-align: left; color: gray; padding: 3rem  0 0 3rem; 
                  margin-bottom: 3rem;"></div>
      <el-form label-width="100px" inline="true" style="text-align:left;padding-left: 3rem;">
        <el-form-item label="" style="width: 100%;">
          <el-input v-model="identity.IdentityName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" style="width: 100%;">
          <el-input v-model="identity.IdentityNum" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" style="width: 30%;">
          <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :before-upload="beforeAvatarImgFUpload">
            <img v-if="identity.IdentityImgF" :src="identity.IdentityImgF" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label=":" style="width: 30%;">
          <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :before-upload="beforeAvatarImgBUpload">
            <img v-if="identity.IdentityImgB" :src="identity.IdentityImgB" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label=":" style="width: 30%;">
          <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :before-upload="beforeAvatarLicenseImgUpload">
            <img v-if="identity.LicenseImg" :src="identity.LicenseImg" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-input v-model="identity.LicenseCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-input v-model="identity.LicenseName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-input v-model="identity.LicenseOwner" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-date-picker
            type="date"
            v-model="identity.AvailableStartTime"
            aria-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-date-picker
            type="date"
            v-model="identity.AvailableTime"
            aria-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label=":" style="width: 100%;">
          <el-input v-model="identity.Tel" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div class="auto_ten">
        <el-button type="primary" @click="sendIdentity"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
  import * as merchantApi from '../../api/Merchant';
  import * as imgUploadApi from '../../api/img';
  import * as identityApi from '../../api/Identity';

  export default {
    data() {
      return {
        identity: {
          MerchantId: this.$store.getters.getMerchantId,
          IdentityName: "",
          IdentityNum: "",
          IdentityImgF: "",
          IdentityImgB: "",
          LicenseImg: "",
          LicenseCode: "",
          LicenseName: "",
          LicenseOwner: "",
          AvailableStartTime: "",
          AvailableTime: "",
          Tel: ""
        }
      }
    },
    methods: {
      beforeAvatarImgFUpload(file) {
        const is = file.type === 'image/jpeg' || file.type === 'image/png';

        var form = new FormData();

        form.append("file", file);

        if (!is) {
          this.$message.error('Jpg!');
          return
        }

        imgUploadApi.ImgUpload(form).then(data => {
          if(data.status == 401) {
            identityApi.GetTokenByRefreshToken(this.$store.getters.getRefreshToken).then(res => {
              if(res.status == 400) this.$router.push('/Customer/SignIn');

              else {
                this.$store.dispatch('commitRefreshToken', res.body.refresh_token);
                this.$store.dispatch('commitToken', res.body.access_token);

                imgUploadApi.ImgUpload(form).then(data => {
                  this.identity.IdentityImgF = data.body;
                })
              }
            })
          }
          this.identity.IdentityImgF = data.body;
        })

        return is;
      },

      beforeAvatarImgBUpload(file) {
        const is = file.type === 'image/jpeg' || file.type === 'image/png';

        var form = new FormData();

        form.append("file", file);

        if (!is) {
          this.$message.error('Jpg!');
          return
        }

        imgUploadApi.ImgUpload(form).then(data => {
          if(data.status == 401) {
            identityApi.GetTokenByRefreshToken(this.$store.getters.getRefreshToken).then(res => {
              if(res.status == 400) this.$router.push('/Customer/SignIn');

              else {
                this.$store.dispatch('commitRefreshToken', res.body.refresh_token);
                this.$store.dispatch('commitToken', res.body.access_token);

                imgUploadApi.ImgUpload(form).then(data => {
                  this.identity.IdentityImgB = data.body;
                })
              }
            })
          }
          this.identity.IdentityImgB = data.body;
        })

        return is;
      },

      beforeAvatarLicenseImgUpload(file) {
        const is = file.type === 'image/jpeg' || file.type === 'image/png';

        var form = new FormData();

        form.append("file", file);

        if (!is) {
          this.$message.error('Jpg!');
          return
        }

        imgUploadApi.ImgUpload(form).then(data => {
          if(data.status == 401) {
            identityApi.GetTokenByRefreshToken(this.$store.getters.getRefreshToken).then(res => {
              if(res.status == 400) this.$router.push('/Customer/SignIn');

              else {
                this.$store.dispatch('commitRefreshToken', res.body.refresh_token);
                this.$store.dispatch('commitToken', res.body.access_token);

                imgUploadApi.ImgUpload(form).then(data => {
                  this.identity.LicenseImg = data.body;
                })
              }
            })
          }
          this.identity.LicenseImg = data.body;
        })

        return is;
      },

      sendIdentity() {
        if(this.identity.MerchantId == "" || this.identity.IdentityName == "" || this.identity.IdentityNum == "" ||
           this.identity.IdentityImgF == "" || this.identity.IdentityImgB == "" || this.identity.LicenseImg == "" ||
           this.identity.LicenseCode == "" || this.identity.LicenseName == "" || this.identity.LicenseOwner == "" ||
           this.identity.AvailableStartTime == "" || this.identity.AvailableTime == "" || this.identity.Tel == "") {
           this.$message.error("");
           
           return;
        }

        if(/^[A-Za-z\u4e00-\u9fa5]{1,10}$/.test(this.identity.IdentityName) &&
           /^[0-9]{18}$/.test(this.identity.IdentityNum) && 
           /^[0-9A-Za-z]{15}$/.test(this.identity.LicenseCode) &&
           /^[A-Za-z\u4e00-\u9fa5]{1,15}$/.test(this.identity.LicenseName) &&
           /^[A-Za-z\u4e00-\u9fa5]{1,10}$/.test(this.identity.LicenseOwner) &&
           /^\d{11}$/.test(this.identity.Tel)) {
          merchantApi.addIdentity(this.identity).then((res) => {
            if(res == 401) {
              identityApi.GetTokenByRefreshToken(this.$store.getters.getRefreshToken).then(res => {
                if(res.status == 400) this.$router.push('/Customer/SignIn');

                else {
                  this.$store.dispatch('commitRefreshToken', res.body.refresh_token);
                  this.$store.dispatch('commitToken', res.body.access_token);
                  
                  merchantApi.addIdentity(this.identity).then((res) => {
                    if(res !== 200) this.$message.error("");

                    else {
/*                       merchantApi.checkIdentity({
                        "MerchantId": this.$store.getters.getMerchantId,
                        "CheckResult": true
                      }).then(res => {
                        if(res.status != 200) this.$message.error();

                        else this.$router.push('/Merchant/Operation');
                      }) */
                      this.$router.push('/Merchant/Operation/Welcome');
                    }
                  })
                }
              })
            }
            else if(res !== 200) this.$message.error("");

            else {
/*               merchantApi.checkIdentity({
                "MerchantId": this.$store.getters.getMerchantId,
                "CheckResult": true
              }).then(res => {
                if(res.status != 200) this.$message.error();

                else this.$router.push('/Merchant/Operation');
              }) */
              this.$router.push('/Merchant/Operation/Welcome');
            }
          });
        } else {
          this.$message.error('');
        }
      }
    }
  }
</script>
<style lang="less">
  @import '../../less/container';
  @import '../../less/ele-ui';
  @import '../../less/formEle';

  @left_three_height : 10rem;
  @left_five_height : 3rem;
</style>