<template>
  <div id="holderMSignUp__place">
    <div class="auto_eight">
      <div id="mleft__place">
        <img src="../../static/imgs/autumn.jpg" alt="" style="width: 80%; height: 30rem; margin-top: 5rem;"/>
      </div>
      <div id="mright__place">
        <div class="formEle_container" style="box-shadow: 3px 3px 3px gray; border: 1px solid gray;">
          <h2></h2>
          <div class="a-element-a-line">
            <input class="rem15-rem2-input" placeholder="" v-model="UserName"/>
            <div class="InputError__Mes" v-if="ShowUserNameError"></div>
          </div>
          <div class="a-element-a-line">
            <input class="rem15-rem2-input" placeholder="" v-model="SignUp.Password" type="password"/>
            <div class="InputError__Mes" v-if="ShowPassError"></div>
          </div>
          <div class="a-element-a-line">
            <input class="rem15-rem2-input" placeholder="" v-model="SignUp.ConfirmedPassword" type="password"/>
            <div class="InputError__Mes" v-if="ShowPassComfiredError"></div>
          </div>
          <div class="a-element-a-line">
            <div class="rem15-rem2-button" v-on:click="RegisterRequest"></div>
            <div class="InputError__Mes" v-if="ShowSignUpError"></div> 
            <div class="NormalSuccess__Mes" v-if="ShopSignUpSuccess"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as SignUpReq from '../../api/Identity';

  export default {
    data() {
      return {
        SignUp: {
          Email: "",
          PhoneNumber: "",
          Password: "",
          ConfirmedPassword: "",
          Role: "merchant"
        },
        ShowUserNameError: false,
        ShowPassError: false,
        ShowPassComfiredError: false,
        ShowSignUpError: false,
        ShopSignUpSuccess: false,
        UserName: ""
      }
    },

    methods: {
      RegisterRequest() {
        if(/^[0-9a-zA-Z-_]+@[0-9a-zA-Z-_]+(\.[0-9A-Za-z-_]+)+$/.test(this.UserName))
        {
          this.SignUp.Email = this.UserName;
        }

        else if(/^1[34567]\d{9}$/.test(this.UserName))
        {
          this.SignUp.PhoneNumber = this.UserName;
        }

        else {
          this.ShowUserNameError = true;

          setTimeout(()=> {
            this.ShowUserNameError = false;
          }, 2000);
          return
        }

        if (this.SignUp.Password == "") {
          this.ShowPassError = true;

          setTimeout(()=> {
            this.ShowPassError = false;
          }, 2000);
          return;
        }
        if (this.SignUp.ConfirmedPassword !==this.SignUp.Password) {
          this.ShowPassComfiredError = true;

          setTimeout(()=> {
            this.ShowPassComfiredError = false;
          }, 2000);
          return;
        }
        
        SignUpReq.SignUpRequest(this.SignUp).then(res => {
          if(res.status == 400) {
            if(res.body == 'Already exist') this.$message.error("");

            this.ShowSignUpError = true;
            setTimeout(()=> {
              this.ShowSignUpError = false;
            }, 2000);

            this.SignUp = {
              Email: "",
              PhoneNumber: "",
              Password: "",
              ConfirmedPassword: "",
              Role: "merchant"
            }

            this.UserName = "";
          }

          else if(res.status == 200) {
            this.SignUp = {
              Email: "",
              PhoneNumber: "",
              Password: "",
              ConfirmedPassword: "",
              Role: "merchant"
            }

            this.UserName = "";
            this.$router.push('/Merchant/SignIn');
            this.ShopSignUpSuccess = true;
            setTimeout(()=> {
              this.ShopSignUpSuccess = false;
            }, 2000);
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../less/container.less";
  @import "../../less/formEle.less";
  @import "../../less/msg.less";

  @eight_height : 40rem;
  @top_distance : 15rem;
  @left_eight_height : auto;
  @right_two_height : auto;
  @left_five_height : auto;
  
  #holderMSignUp__place {
    height: auto;
    overflow: hidden;
  }

  #mleft__place {
    float: left;
    width: 65%;
    height: 40rem;
    line-height: 40rem;
    border-right: 1px solid lightgray;
  }
  #mleft__place #merchant_logo{
    font-size: 5rem;
  }

  #mright__place {
    float: right;
    width: 30%;
    height: 40rem;
  }
</style>
